export default (str) => {
  return [...str].map((v, i, t) => {
    let operators = ['&', '|', '^']
    let op = operators[Math.floor(Math.random() * operators.length)]
    let vCode = v.charCodeAt(0)
    return i
           ? eval(`String.fromCharCode(${vCode} ${op} ${t[i - 1].charCodeAt(0)})`)
           : eval(`String.fromCharCode(${vCode} ${op} ${t[t.length - 1].charCodeAt(0)})`)
  }).join('')
}